//
if (document.querySelector('img[loading=lazy]')) {
   document.addEventListener('DOMContentLoaded', function () {
      let lazyImages = [].slice.call(document.querySelectorAll('img[loading=lazy]'));
      let active = false;
      const lazyLoad = function () {
         if (active === false) {
            active = true;
            setTimeout(function () {
               lazyImages.forEach(function (lazyImage) {
                  if ((lazyImage.getBoundingClientRect().top <= window.innerHeight && lazyImage.getBoundingClientRect().bottom >= 0) && getComputedStyle(lazyImage).opacity !== '0') {
                     lazyImage.src = lazyImage.dataset.src;
                     lazyImage.srcset = lazyImage.dataset.srcset;
                     lazyImages = lazyImages.filter(function (image) {
                        return image !== lazyImage;
                     });
                     if (lazyImages.length === 0) {
                        document.removeEventListener('scroll', lazyLoad);
                        window.removeEventListener('resize', lazyLoad);
                        window.removeEventListener('orientationchange', lazyLoad);
                     }
                  }
               });
               active = false;
            }, 100);
         }
      };
      document.addEventListener('scroll', lazyLoad);
      window.addEventListener('resize', lazyLoad);
      window.addEventListener('load', lazyLoad);
      window.addEventListener('orientationchange', lazyLoad);
   });
}
// burger menu
if (document.querySelector('.nav')) {
   function burgerMenu() {
      const burger = document.querySelector('.burger')
      const menu = document.querySelector('.nav__menu')
      const body = document.querySelector('body')
      burger.addEventListener('click', () => {
         if (!menu.classList.contains('active')) {
            menu.classList.add('active')
            burger.classList.add('active')
            body.classList.add('locked')
         } else {
            menu.classList.remove('active')
            burger.classList.remove('active')
            body.classList.remove('locked')
         }
      })
   }
   burgerMenu();
}
// nav select
if (document.querySelector('.select')) {
   var x, i, j, l, ll, selElmnt, a, b, c;
   x = document.getElementsByClassName('select');
   l = x.length;
   for (i = 0; i < l; i++) {
      selElmnt = x[i].getElementsByTagName('select')[0];
      ll = selElmnt.length;
      a = document.createElement('DIV');
      a.setAttribute('class', 'select-selected nav__text');
      a.innerHTML = selElmnt.options[selElmnt.selectedIndex].innerHTML;
      x[i].appendChild(a);
      b = document.createElement('DIV');
      b.setAttribute('class', 'select-items select-hide nav__text');
      for (j = 1; j < ll; j++) {
         c = document.createElement('DIV');
         c.innerHTML = selElmnt.options[j].innerHTML;
         c.addEventListener('click', function (e) {
            var y, i, k, s, h, sl, yl;
            s = this.parentNode.parentNode.getElementsByTagName('select')[0];
            sl = s.length;
            h = this.parentNode.previousSibling;
            for (i = 0; i < sl; i++) {
               if (s.options[i].innerHTML == this.innerHTML) {
                  s.selectedIndex = i;
                  h.innerHTML = this.innerHTML;
                  y = this.parentNode.getElementsByClassName('same-as-selected');
                  yl = y.length;
                  for (k = 0; k < yl; k++) {
                     y[k].removeAttribute('class');
                  }
                  this.setAttribute('class', 'same-as-selected');
                  break;
               }
            }
            h.click();
         });
         b.appendChild(c);
      }
      x[i].appendChild(b);
      a.addEventListener('click', function (e) {
         e.stopPropagation();
         closeAllSelect(this);
         this.nextSibling.classList.toggle('select-hide');
         this.classList.toggle('select-arrow');
      });
   }
   function closeAllSelect(elmnt) {
      var x, y, i, xl, yl, arrNo = [];
      x = document.getElementsByClassName('select-items');
      y = document.getElementsByClassName('select-selected');
      xl = x.length;
      yl = y.length;
      for (i = 0; i < yl; i++) {
         if (elmnt == y[i]) {
            arrNo.push(i)
         } else {
            y[i].classList.remove('select-arrow');
         }
      }
      for (i = 0; i < xl; i++) {
         if (arrNo.indexOf(i)) {
            x[i].classList.add('select-hide');
         }
      }
   }
   document.addEventListener('click', closeAllSelect);
}
// nav dropdown
if (document.querySelector('.dropdown')) {
   function accordion() {
      const items = document.querySelectorAll('.dropdown__header')
      items.forEach(item => {
         item.addEventListener('click', () => {
            const parent = item.parentNode
            if (parent.classList.contains('show')) {
               parent.classList.remove('show')
            } else {
               document
                  .querySelectorAll('.dropdown__body')
                  .forEach(child => child.classList.remove('show'))
               parent.classList.add('show')
            }
         })
      })
   }
   accordion()
}
// scroll link
const links = document.querySelectorAll('.scroll');
for (const link of links) {
   link.addEventListener('click', clickHandler);
}
function clickHandler(e) {
   e.preventDefault();
   const theLink = this.getAttribute('href');
   document.querySelector(theLink).scrollIntoView({
      behavior: 'smooth'
   });
}
